* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Manrope, sans-serif;
}

html {
  font-size: 62.5%;
}

.container {
  width: 100%;
  height: 100vh;
  color: #eee;
  background-color: #12121f;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calendar {
  width: 45rem;
  height: 52rem;
  background-color: #222227;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 .5rem 3rem rgba(0, 0, 0, .4);
}

.month-year {
  display: flex;
}

.month, .year {
  width: 50%;
  height: 12rem;
  text-align: center;
  text-shadow: 0 .3rem .5rem rgba(0, 0, 0, .5);
  background-color: #167e56;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  display: flex;
}

.month i, .year i {
  cursor: pointer;
  font-size: 2.5rem;
}

.month h1 {
  text-transform: uppercase;
  letter-spacing: .2rem;
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

.year h2 {
  text-transform: uppercase;
  letter-spacing: .2rem;
  margin-bottom: 1rem;
  font-size: 3rem;
  font-weight: 400;
}

.month p {
  font-size: 1.6rem;
}

.weekdays {
  width: 100%;
  height: 5rem;
  align-items: center;
  padding: 0 .4rem;
  display: flex;
}

.weekdays div {
  letter-spacing: .1rem;
  width: 6.31429rem;
  text-shadow: 0 .3rem .5rem rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  font-weight: 400;
  display: flex;
}

.days {
  width: 100%;
  flex-wrap: wrap;
  padding: .2rem;
  display: flex;
}

.days div {
  width: 5.74286rem;
  height: 5rem;
  text-shadow: 0 .3rem .5rem rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  margin: .3rem;
  font-size: 1.4rem;
  transition: background-color .2s;
  display: flex;
}

.days div:hover:not(.today) {
  cursor: pointer;
  background-color: #262626;
  border: .2rem solid #777;
}

.prev-date, .next-date {
  opacity: .5;
}

.today {
  background-color: #167e56;
}

